import React, { useState } from "react";
import axios from "axios";
import { Redirect } from "react-router-dom";
function Register() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    tel: "",
    date: "",
    url: "",
    isFound: false,
    isupdate: false,
  });
  const { name, email, tel, date, url } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });
  const onSubmit = (e) => {
    e.preventDefault();
    axios
      .post("http://localhost:5000/registerForm", {
        name: formData.name,
        email: formData.email,
        tel: formData.tel,
        date: formData.date,
        url: formData.url,
      })
      .then((res) => {
        if (res.data === "Submitted") {
          alert("Your data is submitted");
          setFormData({ ...formData, isupdate: true });
        }
      })
      .catch((err) => {
        console.error(err);
      });
  };
  if (formData.isupdate === true) return <Redirect to={{ pathname: "/" }} />;
  return (
    <div className="container">
      <h1>Registration Form</h1>
      <hr></hr>
      <form className="form" onSubmit={(e) => onSubmit(e)}>
        <div className="form-group">
          <input
            type="text"
            placeholder="Name"
            name="name"
            value={name}
            onChange={(e) => onChange(e)}
            required
          />
        </div>
        <div className="form-group">
          <input
            type="email"
            placeholder="Alternate-Email Address"
            name="email"
            value={email}
            onChange={(e) => onChange(e)}
            required
          />
        </div>
        <div className="form-group">
          <input
            type="tel"
            placeholder="Mobile Number"
            name="tel"
            value={tel}
            pattern="^[1-9]\d*$"
            minLength="10"
            maxLength="10"
            onChange={(e) => onChange(e)}
            required
          />
        </div>
        <div className="form-group">
          <input
            type="date"
            placeholder="01-01-1999"
            name="date"
            value={date}
            onChange={(e) => onChange(e)}
            required
          />
        </div>
        <div className="form-group">
          <input
            type="url"
            placeholder="URL"
            name="url"
            value={url}
            onChange={(e) => onChange(e)}
            required
          />
        </div>
        <input type="submit" className="btn-btn" value="Register" />
      </form>
    </div>
  );
}

export default Register;
